import React from 'react';
import PropTypes from 'prop-types';

const ListItem = ({note}) => {
  let { price, genre, grade, subject } = note;

  if (grade.length < 2) {
    grade += ' Класс';
  } else {
    let str = grade.split(';'),
        firstIdx = str[0],
        lastIdx = str[str.length - 1];

    if (firstIdx === lastIdx) {
      grade = lastIdx + ' Класс';
    } else {
      grade = str[0] + '-' +  str[str.length - 1] + ' Классов';
    }
  }

  return (
    <div className="courses-list__item">
      <div className="course">
        <div className="course__image-wrap">
          <img className="course__image" src="https://www.imumk.ru/svc/coursecover/4306" />
        </div>
        <span className="course__title">{subject}</span>
        <span className="course__grade">{grade}</span>
        <span className="course__genre">{genre}</span>
        <button className="course__price">{price} р.</button>
      </div>
    </div>
  )
};

ListItem.propTypes = {
  note: PropTypes.object,
};

export default ListItem;
