/* eslint-disable import/no-named-as-default */
// import PropTypes from "prop-types";
import React, {Component} from "react";
import { hot } from "react-hot-loader";

import List from './List';
import Tools from './Tools';

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      subject: 'Все предметы',
      genres: 'Все жанры',
      grades: 'Все классы',
    }
  }

  changeSubject = (e) => {
    this.setState({
      subject: e.target.value,
    })
  }

  changeGenre = (e) => {
    this.setState({
      genres: e.target.value,
    })
  }

  changeGrade = (e) => {
    this.setState({
      grades: e.target.value,
    })
  }

  render() {

    let { subject, genres, grades } = this.state;

    return (
      <div className="content">
        <Tools onChange={this.changeSubject} onChangeGenre={this.changeGenre} onChangeGrade={this.changeGrade}/>
        <List subject={subject} genres={genres} grades={grades}/>
      </div>
    );
  }
}

export default hot(module)(App);
