import React, {Component} from "react";
import ListItem from './ListItem';
import PropTypes from 'prop-types';

export default class List extends Component  {
  constructor(props) {
    super(props);

    this.state = {
      notebooks: []
    }
  }
  componentDidMount() {
    const URL = 'http://krapipl.imumk.ru:8082/api/mobilev1/update';

    fetch(URL, {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({'data': ''})
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        this.setState({
          notebooks: data.items
        });
      });
  }

  render() {
    let { subject, genres, grades } = this.props;
    let { notebooks } = this.state;

    let filterResult = notebooks;

    //DON`T REPEAT YOURSELF!!!
    if (subject !== 'Все предметы') {
      filterResult = filterResult.filter(function(item) {
        if (item.subject === subject) {
          return item;
        }
      });
    }

    if (genres !== 'Все жанры') {
      filterResult = filterResult.filter(function(item) {
        if (item.genre === genres) {
          return item;
        }
      });
    }


    if (grades !== 'Все классы') {
      filterResult = filterResult.filter(function(item) {
        let gradesArr = item.grade.split(';');
        for (let grade of gradesArr) {
          if (grade === grades) {
            return item;
          }
        }
      });
    }

    filterResult = filterResult.map(function(note, index) {
      return (
        <ListItem key={index} note={note}/>
      );
    });
    return (
      <div className="courses-list">{filterResult}</div>
    )
  }
}

List.propTypes = {
  subject: PropTypes.string,
  genres: PropTypes.string,
  grades: PropTypes.string,
}
